<template>
    <div class="real-time-component-list">
        <div
            v-for="(item, index) in componentData"
            :key="index"
            class="list"
            :class="{ actived: transformIndex(index) === curComponentIndex }"
            @click="onClick(transformIndex(index))"
        >
            <span class="iconfont" :class="'icon-' + getComponent(index).icon"></span>
            <span>{{ getComponent(index).label }} {{ index }}</span>
            <div class="icon-container">
                <template v-if="!getComponent(index).isLock">
                    <span title = "向上" class="el-icon-arrow-up handlitem" @click="upComponent(transformIndex(index))"></span>
                    <span title = "向下" class="el-icon-arrow-down handlitem" @click="downComponent(transformIndex(index))"></span>
                    <span title = "删除" class="el-icon-delete handlitem" @click="deleteComponent(transformIndex(index))"></span>
                    <span title = "锁定" class="el-icon-lock handlitem" @click="lock(transformIndex(index))"></span>
                </template>
                <span v-else 
                      title = "解锁" 
                      class="el-icon-unlock handlitem" 
                      @click="unlock(transformIndex(index))"></span>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
    computed: mapState([
        'componentData',
        'curComponent',
        'curComponentIndex',
    ]),
    methods: {
        getComponent(index) {
            let newIndex = this.transformIndex(index)
            return this.componentData[newIndex]
        },

        transformIndex(index) {
            // 后放进去的在上面，所以需要一次倒排
            return this.componentData.length - 1 - index
        },

        onClick(index) {
            this.setCurComponent(index)
        },

        unlock() {
            setTimeout(() => {
                this.$store.commit('unlock')
            })
        },

        lock() {
            setTimeout(() => {
                this.$store.commit('lock')
            })
        },

        deleteComponent() {
            setTimeout(() => {
                this.$store.commit('deleteComponent')
            })
        },

        upComponent() {
            setTimeout(() => {
                this.$store.commit('upComponent')
            })
        },

        downComponent() {
            setTimeout(() => {
                this.$store.commit('downComponent')
            })
        },

        setCurComponent(index) {
            this.$store.commit('setCurComponent', { component: this.componentData[index], index })
        },
    },
}
</script>

<style lang="scss" scoped>
.real-time-component-list {
    height: 35%;

    .list {
        height: 30px;
        cursor: grab;
        text-align: center;
        color: #333;
        display: flex;
        align-items: center;
        font-size: 12px;
        padding: 0 10px;
        position: relative;
        user-select: none;

        &:active {
            cursor: grabbing;
        }

        &:hover {
            background-color: rgba(200, 200, 200, .2);

            .icon-container {
                display: block;
            }
        }

        .iconfont {
            margin-right: 4px;
            font-size: 16px;
        }

        .icon-wenben,
        .icon-tupian {
            font-size: 14px;
        }

        .icon-container {
            position: absolute;
            right: 10px;
            display: none;

            .iconfont {
                cursor: pointer;
            }

            .handlitem {
                margin-left: 5px;
            }
        }
    }

    .actived {
        background: #ecf5ff;
        color: #409eff;
    }
}
</style>
