<template>
    <div v-show="menuShow" class="contextmenu" :style="{ top: menuTop + 'px', left: menuLeft + 'px' }">
        <ul @mouseup="handleMouseUp">
            <template v-if="curComponent">
                <template v-if="!curComponent.isLock">
                    <li class="el-icon-top" @click="topComponent"> 置顶</li>
                    <li class="el-icon-bottom" @click="bottomComponent"> 置底</li>
                    <li class="el-icon-arrow-up" @click="upComponent"> 上移</li>
                    <li class="el-icon-arrow-down" @click="downComponent"> 下移</li>
                    <el-divider></el-divider>   
                    <li class="el-icon-document-copy" @click="copy"> 复制</li>
                    <li class="el-icon-copy-document" @click="paste"> 粘贴</li>
                    <li class="el-icon-scissors" @click="cut"> 剪切</li>
                    <li class="el-icon-delete" @click="deleteComponent"> 删除</li>
                    <el-divider></el-divider>
                    <li class="el-icon-lock" @click="lock"> 加锁</li>
                </template>
                <li v-else class="el-icon-unlock" @click="unlock"> 解锁</li>
            </template>
            <li v-else class="el-icon-copy-document" @click="paste"> 粘贴</li>
        </ul>
    </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
    data() {
        return {
            copyData: null,
        }
    },
    computed: mapState([
        'menuTop',
        'menuLeft',
        'menuShow',
        'curComponent',
    ]),
    methods: {
        // 点击菜单时不取消当前组件的选中状态
        handleMouseUp() {
            this.$store.commit('setClickComponentStatus', true)
        },

        deleteComponent() {
            this.$store.commit('deleteComponent')
        },

        upComponent() {
            this.$store.commit('upComponent')
        },

        downComponent() {
            this.$store.commit('downComponent')
        },

        topComponent() {
            this.$store.commit('topComponent')
        },

        bottomComponent() {
            this.$store.commit('bottomComponent')
        },

        copy() {
            this.$store.commit('copy')
        },

        paste() {
            this.$store.commit('copy')
        },

        cut() {
            this.$store.commit('cut')
        },
        
        lock() {
            this.$store.commit('lock')
        },

        unlock() {
            this.$store.commit('unlock')
        },
    },
}
</script>

<style lang="scss" scoped>
.contextmenu {
    position: absolute;
    z-index: 1000;

    ul {
        border: 1px solid #e4e7ed;
        border-radius: 4px;
        background-color: #fff;
        box-shadow: 0 2px 12px 0 rgba(0, 0, 0, .1);
        box-sizing: border-box;
        margin: 5px 0;
        padding: 6px 0;

        li {
            font-size: 14px;
            padding: 0 20px;
            position: relative;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            color: #606266;
            height: 30px;
            line-height: 30px;
            box-sizing: border-box;
            cursor: pointer;
            display: block;

            &:hover {
                background-color: #f5f7fa;
            }
        }

        .el-divider--horizontal {
            margin: 10px 0;
        }
    }
}
</style>